"use client"

import type React from "react"

import { useState } from "react"
import { useForm } from "react-hook-form"
import { zodResolver } from "@hookform/resolvers/zod"
import { z } from "zod"
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
} from "@/components/ui/dialog"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { Plus } from "lucide-react"

const productSchema = z.object({
  name: z.string().min(3, "Nome deve ter pelo menos 3 caracteres"),
  sku: z.string().min(2, "SKU é obrigatório"),
  categoryId: z.string().min(1, "Categoria é obrigatória"),
  purchasePrice: z.coerce.number().positive("Preço de compra deve ser positivo"),
  salePrice: z.coerce.number().positive("Preço de venda deve ser positivo"),
  minStock: z.coerce.number().nonnegative("Estoque mínimo não pode ser negativo"),
  description: z.string().optional(),
})

type ProductFormData = z.infer<typeof productSchema>

interface ProductDialogProps {
  onSuccess?: () => void
  onSubmit: (data: ProductFormData) => Promise<void>
  isLoading?: boolean
  trigger?: React.ReactNode
}

export function ProductDialog({ onSuccess, onSubmit, isLoading = false, trigger }: ProductDialogProps) {
  const [open, setOpen] = useState(false)
  const {
    register,
    handleSubmit,
    formState: { errors },
    reset,
  } = useForm<ProductFormData>({
    resolver: zodResolver(productSchema),
  })

  const handleFormSubmit = async (data: ProductFormData) => {
    try {
      await onSubmit(data)
      reset()
      setOpen(false)
      onSuccess?.()
    } catch (error) {
      console.error("Erro ao salvar produto:", error)
    }
  }

  return (
    <Dialog open={open} onOpenChange={setOpen}>
      <DialogTrigger asChild>
        {trigger || (
          <Button className="gap-2">
            <Plus className="w-4 h-4" />
            Novo Produto
          </Button>
        )}
      </DialogTrigger>
      <DialogContent className="max-w-md">
        <DialogHeader>
          <DialogTitle>Novo Produto</DialogTitle>
          <DialogDescription>Adicione um novo produto ao catálogo</DialogDescription>
        </DialogHeader>
        <form onSubmit={handleSubmit(handleFormSubmit)} className="space-y-4">
          <div className="space-y-2">
            <Label htmlFor="name">Nome do Produto</Label>
            <Input id="name" placeholder="Ex: MacBook Pro" {...register("name")} />
            {errors.name && <p className="text-sm text-destructive">{errors.name.message}</p>}
          </div>

          <div className="space-y-2">
            <Label htmlFor="sku">SKU</Label>
            <Input id="sku" placeholder="Ex: MB-PRO-M3" {...register("sku")} />
            {errors.sku && <p className="text-sm text-destructive">{errors.sku.message}</p>}
          </div>

          <div className="space-y-2">
            <Label htmlFor="categoryId">Categoria</Label>
            <Select {...register("categoryId")}>
              <SelectTrigger>
                <SelectValue placeholder="Selecione uma categoria" />
              </SelectTrigger>
              <SelectContent>
                <SelectItem value="1">Eletrônicos</SelectItem>
                <SelectItem value="2">Periféricos</SelectItem>
                <SelectItem value="3">Vestuário</SelectItem>
              </SelectContent>
            </Select>
            {errors.categoryId && <p className="text-sm text-destructive">{errors.categoryId.message}</p>}
          </div>

          <div className="grid grid-cols-2 gap-4">
            <div className="space-y-2">
              <Label htmlFor="purchasePrice">Preço de Compra</Label>
              <Input id="purchasePrice" type="number" step="0.01" placeholder="0.00" {...register("purchasePrice")} />
              {errors.purchasePrice && <p className="text-sm text-destructive">{errors.purchasePrice.message}</p>}
            </div>
            <div className="space-y-2">
              <Label htmlFor="salePrice">Preço de Venda</Label>
              <Input id="salePrice" type="number" step="0.01" placeholder="0.00" {...register("salePrice")} />
              {errors.salePrice && <p className="text-sm text-destructive">{errors.salePrice.message}</p>}
            </div>
          </div>

          <div className="space-y-2">
            <Label htmlFor="minStock">Estoque Mínimo</Label>
            <Input id="minStock" type="number" placeholder="10" {...register("minStock")} />
            {errors.minStock && <p className="text-sm text-destructive">{errors.minStock.message}</p>}
          </div>

          <div className="space-y-2">
            <Label htmlFor="description">Descrição</Label>
            <Input id="description" placeholder="Descrição do produto" {...register("description")} />
          </div>

          <div className="flex gap-2 justify-end pt-4">
            <Button type="button" variant="outline" onClick={() => setOpen(false)}>
              Cancelar
            </Button>
            <Button type="submit" disabled={isLoading}>
              {isLoading ? "Salvando..." : "Salvar"}
            </Button>
          </div>
        </form>
      </DialogContent>
    </Dialog>
  )
}
