"use client"

import { useEffect, useState } from "react"
import { useRouter } from "next/navigation"
import { Header } from "@/components/header"
import { Sidebar } from "@/components/sidebar"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { DataTable } from "@/components/data-table"
import { Card } from "@/components/ui/card"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { ProductDialog } from "@/components/dialogs/product-dialog"
import { Edit, Trash2, MoreVertical } from "lucide-react"
import { DropdownMenu, DropdownMenuContent, DropdownMenuItem, DropdownMenuTrigger } from "@/components/ui/dropdown-menu"
import { toast } from "sonner"
import { 
  useProducts, 
  useCreateProduct,
  useDeleteProduct,
  useUpdateProduct,
} from "@/lib/hooks/use-products"

const mockProducts = [
  {
    id: "1",
    name: "MacBook Pro M3",
    sku: "MB-PRO-M3",
    category: "Eletrônicos",
    stock: 15,
    price: 8900.0,
    active: true,
  },
  {
    id: "2",
    name: "Mouse Wireless",
    sku: "MW-001",
    category: "Periféricos",
    stock: 0,
    price: 89.9,
    active: true,
  },
  {
    id: "3",
    name: "Teclado Mecânico",
    sku: "TM-RGB",
    category: "Periféricos",
    stock: 5,
    price: 450.0,
    active: false,
  },
]

export default function ProductsPage() {
  const router = useRouter()
  const [search, setSearch] = useState("")
  const [editingProduct, setEditingProduct] = useState<any | null>(null)
  const [category, setCategory] = useState("all")
  const [products, setProducts] = useState(mockProducts)

  useEffect(() => {
    const token = localStorage.getItem("token")
    if (!token) {
      router.push("/login")
    }
  }, [router])

  // Hooks da API
  

  const filteredProducts = products.filter((product) => {
    const matchesSearch =
      product.name.toLowerCase().includes(search.toLowerCase()) ||
      product.sku.toLowerCase().includes(search.toLowerCase())
    const matchesCategory = category === "all" || product.category === category
    return matchesSearch && matchesCategory
  })

  const handleAddProduct = async (data: any) => {
    // Mock API call - replace with real API
    const newProduct = {
      id: String(products.length + 1),
      ...data,
      stock: 0,
      active: true,
    }
    setProducts([...products, newProduct])
    toast.success("Produto criado com sucesso!")
  }

  const handleDeleteProduct = (id: string) => {
    setProducts(products.filter((p) => p.id !== id))
    toast.success("Produto deletado com sucesso!")
  }

  const columns = [
    {
      key: "name" as const,
      label: "Produto",
      render: (value: string) => <span className="font-medium">{value}</span>,
    },
    { key: "sku" as const, label: "SKU" },
    { key: "category" as const, label: "Categoria" },
    {
      key: "stock" as const,
      label: "Estoque",
      render: (value: number) => <span className={value === 0 ? "text-red-600 font-medium" : ""}>{value} un</span>,
    },
    {
      key: "price" as const,
      label: "Preço",
      render: (value: number) => `R$ ${value.toFixed(2)}`,
    },
    {
      key: "active" as const,
      label: "Status",
      render: (value: boolean) => (
        <span
          className={`px-2 py-1 rounded text-sm font-medium ${value ? "bg-green-100 text-green-700" : "bg-gray-100 text-gray-700"}`}
        >
          {value ? "Ativo" : "Inativo"}
        </span>
      ),
    },
  ]

  return (
    <div className="flex h-screen bg-background">
      <Sidebar />
      <div className="flex-1 flex flex-col overflow-hidden">
        <Header />
        <main className="flex-1 overflow-auto p-6">
          <div className="flex items-center justify-between mb-8">
            <div>
              <h1 className="text-3xl font-bold">Produtos</h1>
              <p className="text-muted-foreground">Gerencie seu catálogo de produtos</p>
            </div>
            <ProductDialog onSubmit={handleAddProduct} />
          </div>

          <Card className="p-6 mb-6">
            <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
              <Input
                placeholder="Buscar por nome ou SKU..."
                value={search}
                onChange={(e) => setSearch(e.target.value)}
              />
              <Select value={category} onValueChange={setCategory}>
                <SelectTrigger>
                  <SelectValue placeholder="Categoria" />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="all">Todas as Categorias</SelectItem>
                  <SelectItem value="Eletrônicos">Eletrônicos</SelectItem>
                  <SelectItem value="Periféricos">Periféricos</SelectItem>
                  <SelectItem value="Vestuário">Vestuário</SelectItem>
                </SelectContent>
              </Select>
              <Button variant="outline" className="w-full bg-transparent">
                Filtrar
              </Button>
            </div>
          </Card>

          <DataTable
            data={filteredProducts}
            columns={columns}
            pageSize={10}
            actions={(product) => (
              <DropdownMenu>
                <DropdownMenuTrigger asChild>
                  <Button variant="ghost" size="sm">
                    <MoreVertical className="w-4 h-4" />
                  </Button>
                </DropdownMenuTrigger>
                <DropdownMenuContent align="end">
                  <DropdownMenuItem>
                    <Edit className="w-4 h-4 mr-2" />
                    Editar
                  </DropdownMenuItem>
                  <DropdownMenuItem>Duplicar</DropdownMenuItem>
                  <DropdownMenuItem>Favoritar</DropdownMenuItem>
                  <DropdownMenuItem onClick={() => handleDeleteProduct(product.id)} className="text-destructive">
                    <Trash2 className="w-4 h-4 mr-2" />
                    Deletar
                  </DropdownMenuItem>
                </DropdownMenuContent>
              </DropdownMenu>
            )}
          />
        </main>
      </div>
    </div>
  )
}
